import React from "react";
import "../Nav/nav.css";
import { Link } from "react-router-dom";

const Nav = ({ setLogin, privateUser,setPrivateUser, setUser }) => {
  return (
    <div>
      <nav className="navbar navbar-expand-sm navbar-light bg-light">
        <div className="container">
          <div className="items">
            <ul className="navbar-nav">
              <Link to="/Home">
                <li className="nav-item">
                  <a className="nav-link active" href="/Home">
                    Home
                  </a>
                </li>
              </Link>
              <Link to="/About">
                <li className="nav-item">
                  <a className="nav-link" href="/About">
                    About
                  </a>
                </li>
              </Link>
              {privateUser && (
                <Link to="/Gallary">
                  <li className="nav-item">
                    <a className="nav-link" href="/Gallary">
                      Gallary
                    </a>
                  </li>
                </Link>
              )}
            </ul>
          </div>

          <Link to="/Login">
            <button
              type="button"
              className="btn btn-primary"
              onClick={() => {
                setLogin(true);
                setUser("");
                setPrivateUser(false);
              }}
            >
              Login
            </button>
          </Link>
        </div>
      </nav>
    </div>
  );
};

export default Nav;
