import React from "react";
import UserDb from "../userData";

const Homepage = () => {

  return (
    <div >
      <form>
        <input list="suggestions" name="options" />
        <datalist id="suggestions">
          {UserDb.map((name, idx) => {
            return <option key={idx} value={name.username} />;
          })}
        </datalist>
      </form>
    </div>
  );
};

export default Homepage;
